/**
 * 数组取差集
 * @param {Array} arr1 目标数组1
 * @param {Array} arr2 目标数组2
 * @return {Array} 数组差集
 */
export default (arr1: Array<any>, arr2: Array<any>): Array<any> => {
    if (({}).toString.call(arr1) !== '[object Array]' || ({}).toString.call(arr2) !== '[object Array]') throw Error('The first argument must be of type Array')

    const s = new Set(arr2)

    return arr1.filter(x => !s.has(x))
}
