/**
 * 整数间汉明距离
 *
 * @param {number} num1 起始数字
 * @param {number} num2 结束数字
 * @return {number} 汉明距离
 */

export default (num1: number, num2: number): number => {
    if (typeof num1! == 'number' || typeof num2! == 'number') return 0

    return ((num1 ^ num2).toString(2).match(/1/g) || '').length
}
