/**
 * 将目标参数date转换成时间戳  暂时只支持  1970-01-01 01:01:01 | 1970年01月01日 01时01分01秒 | new Date()
 * @param {date: Date} date 目标时间格式
 * 
 * @param {format = '-'} format '-' 可传入参数  默认 '-' 
 *
 * @return {number} return 转化后的时间戳
 */
export default (date: any, format: string = '-'): number => {

    if (typeof date === 'string' && format === '-') {

        const replacement: string = date.replace(new RegExp(format, 'gm'), '/');

        return new Date(replacement).getTime();
    }
    if (typeof date === 'string' && format !== '-') {
        const replacement1: string = date.replace(new RegExp(/[年|月]/, "gm"), "-").replace(new RegExp(/[时|分]/, "gm"), ":").replace(new RegExp(/[日|秒]/, "gm"), "")

        const milliseconds: number =  new Date(replacement1).getTime()
        return isNaN(milliseconds) ? 0 : milliseconds;
    }

    if (Object.prototype.toString.call(date) === '[object Date]') {

        return date.getTime();
    }

    return 0;
}