/**
 * 加密手机号
 * 
 * @param {string | number} 目标手机号 
 * @returns 加密后的手机号
 */

export default (tel: number | string) => {
    const types: Array<string> = ['number', 'string']

    if (!types.includes(typeof(tel))) throw Error('tel expects Numbers or strings')

    if (typeof tel === 'number') tel = tel.toString()

    return tel.substr(0, 3) + "****" + tel.substr(7, 4)
}

