/**
 * 字典顺序整理
 */

const strChineseFirstPY: string = ""


const reg: RegExp = /^[\u4E00-\u9FA5A-Za-z]/

/**
 * 获取传入字符串首字母
 * 
 * @param {string} target 
 * @returns 找到的首字母
 */
function makePy(target: string) {

    if (typeof target !== 'string') throw new Error("The expected thing is of type string")

    const ch = target.charAt(0)

    return checkCh(ch)
}

/**
 * 根据字符串首字母Unicode码查询首字母
 * 
 * @param {string} ch 
 * @returns 找到的首字母
 */
function checkCh(ch: string){
    if (!reg.test(ch)) return '#'

    const uni = ch.charCodeAt(0)

    if (uni > 40869 || uni < 19968) return ch

    return strChineseFirstPY.charAt(uni - 19968)
}

export default makePy
