/**
 * 统计数组中特定值出现的次数
 * @param {Array} arr 需要查询的数组
 * @param {Any} value 目标项
 * @return {number} 统计次数
 */

export default (arr: Array<any>, value: any) => {

    if (({}).toString.call(arr) !== '[object Array]') throw Error('The first argument must be of type Array')

    return arr.reduce((prev: number, next: any) => prev + Number(next === value), 0)
}


export const points = (a:string,b:number):string => {
    return a+b;
};
