/**
 * 洗牌算法
 * 
 * @param {any} arr 需要洗牌的数组
 * @returns 洗后数组
 */
export default (arr: Array<any>): Array<any> => {
    for (let i = 0; i < arr.length; i++) {
        const randomIndex = i + Math.floor(Math.random() * (arr.length - i));
        [arr[i], arr[randomIndex]] = [arr[randomIndex], arr[i]]
    }

    return arr
}

