/**
 * ms 转换成天 时 分 秒
 * @param {time: number} time 目标时间ms number类型
 *
 * @return {object} return 转化后的对象
 */

export default (time: number): {[index: string]: string} => { //传入的是ms 秒杀新增天和0.1秒
    const t: number = Math.floor(time / 1000)
    const d: number = Math.floor(t / 60 / 60 / 24)
    const h: number = Math.floor(t / 60 / 60 % 24)
    const m: number = Math.floor(t / 60 % 60)
    const s: number = Math.floor(t % 60)
    const ms: number = Math.floor(time / 100 % 10)
    
    return {
        day: `${d > 9 ? d : '0' + d}`,
        hour: `${h > 9 ? h : '0' + h}`,
        minute: `${m > 9 ? m : '0' + m}`,
        second: `${s > 9 ? s : '0' + s}`,
        ms: ms.toString()
    }
}